
@keyframes App-logo-spin {
    0% {
        transform: translate(0, 0);
    }
    50% {
        transform: translate(0, -10px);
    }
    50% {
        transform: translate(0, 0);
    }
    50% {
        transform: translate(0, 10px);
    }
    100% {
        transform: translate(0, 0);
    }
}

html, body {
    overflow: hidden;
    max-height: 100vh;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.App {
    max-width: 100vw;
    max-height: 100vh;
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    background-color: #28b3fc;
    z-index: 1;
    overflow: hidden;
}

#first-side {
    background-image: linear-gradient(#ff004e, #84006b);
    background-size: auto 200%;
    background-position: top;
    height: 50vh;
    width: 100vw;
    margin: 0;
    border-bottom: 1px solid rgba(0,0,0,.6);
    display: block;
    box-shadow: 0 5px 2px rgba(0,0,0,.4);
    position: absolute;
    top: 0;
    z-index: 4;
}

#second-side {
    background-image: linear-gradient(#ff004e, #84006b);
    background-size: auto 200%;
    background-position: bottom;
    height: 50vh;
    width: 100vw;
    margin: 0;
    border-top: 1px solid rgba(255,255,255,.6);
    display: block;
    box-shadow: 0 -2px 2px rgba(0,0,0,.4);
    position: absolute;
    bottom: 0;
    z-index: 4;
}

#sticker {
    position: absolute;
    left: 0;
    right: 0;
    top: calc(50% - 2.5rem);
    font-size: 5rem;
    text-align: center;
    transform: rotate(-15deg);
    color: #ff2525;
    text-shadow: -3px -3px 0 #fff,3px -3px 0 #fff,-3px 3px 0 #fff,3px 3px 0 #fff,0px 3px 0 #fff, -8px 0px 3px rgba(0,0,0,.4);
    z-index: 5;
}

#cta {
    width: 100%;
    text-align: center;
    position: absolute;
    top: calc(50% - 12rem);
    margin-left: auto;
    margin-right: auto;
    z-index: 6;
    font-size: 5rem;
    text-shadow: 2px 2px 2px rgba(0,0,0,.4);
}


#cta-text {
    height: 3rem;
    font-family: 'Hi Melody', cursive;
    display: block;
    color: white;
}

#cta-arrow {
    margin-top: 2rem;
    font-size: 3rem;
    display: block;
    color: white;
    animation: App-logo-spin infinite 2s linear;
}



@keyframes confettiRain {
    0% {
        opacity: 1;
        margin-top: -100vh;
        margin-left: -200px;
    } 

    100% {
        opacity: 1;
        margin-top: 100vh;
        margin-left: 200px;
    }
}

.confetti {
    opacity: 0;
    position: absolute;
    width: .4rem;
    height: .6rem;
    animation: confettiRain 5s infinite;
}

#confetti-wrapper {
    z-index: 2;
}

::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

#message-wrapper {
    z-index: 3;
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    justify-content: center;
}

#message {
    font-family: 'Hi Melody', cursive;
    padding: 2rem;
    width: 80%;
    border: 1px solid #D7DBE3;
    box-shadow: 0 1px 2px rgba(0,0,0,0.16);
    background-color: #fff;
    border-radius: 0.25rem;
}

.emoji {
    display: block;
    text-align: center;
    font-size: 5rem;
    line-height: 5rem;
}

h1 {
    text-align: center;
    font-family: brandon-grotesque, sans-serif;
    font-weight: 420;
    font-style: normal;
}

p {
    padding: 0; 
    line-height: 1.5rem;
    text-align: center;
    font-family: brandon-grotesque, sans-serif;
    font-weight: 390;
}

.message-btn {
    font-family: brandon-grotesque, sans-serif;
    font-weight: 390;
    font-style: normal;
    display: block;
    margin: 1rem -2rem -2rem -2rem;
    padding: 1rem 2rem;
    font-size: .75rem;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    font-weight: 600;
    border-radius: 0 0 .25rem .25rem;
    background-color: #ff0045;
    text-decoration: none;
}